"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { useToast } from "@/hooks/use-toast";
import { getCodeAnalysis } from "../actions";
import type { AnalyzeCodeOutput } from "@/ai/flows/code-advisor";
import { Wand2, Loader2, AlertTriangle, CheckCircle } from "lucide-react";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Badge } from "@/components/ui/badge";

const codeAdvisorSchema = z.object({
  codebase: z.string().min(50, {
    message: "Please enter a codebase of at least 50 characters for analysis.",
  }),
});

type CodeAdvisorFormValues = z.infer<typeof codeAdvisorSchema>;

export function CodeAdvisorForm() {
  const [analysisResult, setAnalysisResult] = useState<AnalyzeCodeOutput | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const { toast } = useToast();

  const form = useForm<CodeAdvisorFormValues>({
    resolver: zodResolver(codeAdvisorSchema),
    defaultValues: {
      codebase: "",
    },
  });

  async function onSubmit(data: CodeAdvisorFormValues) {
    setIsLoading(true);
    setAnalysisResult(null);
    setError(null);

    const result = await getCodeAnalysis({ codebase: data.codebase });

    setIsLoading(false);

    if (result.error) {
      setError(result.error);
      toast({
        title: "Analysis Failed",
        description: result.error,
        variant: "destructive",
      });
    } else if (result.data) {
      setAnalysisResult(result.data);
      toast({
        title: "Analysis Complete",
        description: "AI code advisor has provided suggestions.",
      });
    }
  }

  const getConfidenceBadgeVariant = (level?: string) => {
    switch (level?.toUpperCase()) {
      case 'HIGH':
        return 'default'; // bg-primary
      case 'MEDIUM':
        return 'secondary'; // bg-secondary
      case 'LOW':
        return 'outline'; // border, text-foreground
      default:
        return 'outline';
    }
  };

  return (
    <Card className="w-full shadow-xl">
      <CardHeader>
        <CardTitle className="text-2xl flex items-center">
          <Wand2 className="mr-2 h-6 w-6 text-accent" /> AI Code Advisor
        </CardTitle>
        <CardDescription>
          Paste your Next.js project codebase (or relevant parts) below. The AI will analyze it and provide suggestions for improvements.
        </CardDescription>
      </CardHeader>
      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)}>
          <CardContent className="space-y-6">
            <FormField
              control={form.control}
              name="codebase"
              render={({ field }) => (
                <FormItem>
                  <FormLabel htmlFor="codebase-input" className="text-lg">Codebase</FormLabel>
                  <FormControl>
                    <Textarea
                      id="codebase-input"
                      placeholder="Paste your code here... (e.g., a specific component, page, or utility function)"
                      className="min-h-[300px] resize-y font-mono text-sm bg-muted/30"
                      {...field}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          </CardContent>
          <CardFooter className="flex justify-end">
            <Button type="submit" disabled={isLoading} size="lg">
              {isLoading ? (
                <>
                  <Loader2 className="mr-2 h-5 w-5 animate-spin" /> Analyzing...
                </>
              ) : (
                <>
                  <Wand2 className="mr-2 h-5 w-5" /> Analyze Code
                </>
              )}
            </Button>
          </CardFooter>
        </form>
      </Form>

      {error && (
        <CardContent>
          <div className="mt-6 p-4 border border-destructive bg-destructive/10 rounded-md">
            <div className="flex items-center text-destructive">
              <AlertTriangle className="mr-2 h-5 w-5" />
              <h3 className="text-lg font-semibold">Analysis Error</h3>
            </div>
            <p className="text-destructive/80">{error}</p>
          </div>
        </CardContent>
      )}

      {analysisResult && (
        <CardContent className="mt-6">
          <h3 className="text-xl font-semibold mb-2 flex items-center">
            <CheckCircle className="mr-2 h-6 w-6 text-green-500" /> Analysis Results
          </h3>
          <div className="mb-4">
            <span className="text-muted-foreground">Confidence Level: </span>
            <Badge variant={getConfidenceBadgeVariant(analysisResult.confidenceLevel)}>
              {analysisResult.confidenceLevel || "N/A"}
            </Badge>
          </div>
          <ScrollArea className="h-[400px] w-full rounded-md border p-4 bg-muted/30">
            {analysisResult.suggestions.length > 0 ? (
              <ul className="space-y-3">
                {analysisResult.suggestions.map((suggestion, index) => (
                  <li key={index} className="pb-3 border-b border-border last:border-b-0">
                    <div className="prose prose-sm dark:prose-invert max-w-none" dangerouslySetInnerHTML={{ __html: suggestion.replace(/\n/g, "<br />") }} />
                  </li>
                ))}
              </ul>
            ) : (
              <p className="text-muted-foreground">No specific suggestions provided.</p>
            )}
          </ScrollArea>
        </CardContent>
      )}
    </Card>
  );
}
