"use server";

import { analyzeCode, type AnalyzeCodeInput, type AnalyzeCodeOutput } from "@/ai/flows/code-advisor";

export async function getCodeAnalysis(
  input: AnalyzeCodeInput
): Promise<{ data?: AnalyzeCodeOutput; error?: string }> {
  try {
    const result = await analyzeCode(input);
    return { data: result };
  } catch (err) {
    console.error("Error in getCodeAnalysis:", err);
    const errorMessage = err instanceof Error ? err.message : "An unknown error occurred.";
    return { error: `Failed to get code analysis: ${errorMessage}` };
  }
}
